p{
  margin: 0;
}

button{
  outline: none;
  cursor: pointer;
  margin: 0;
}

.appMain {
  display: flex;
  background-color: #9bb4eb;
  background-image:url('./assets/sppic.png');
  background-repeat:no-repeat;
  background-position: center;
  background-size: 50vh 30vh;
  height: 100vh;
  justify-content: center;
  align-content: center;
}

.mainInner {
 height: 100%;
 width: 100%;
 flex-direction: column;
}

.innerTop {
  height: 50%;
}

.numberofListsCont {
  height: 60%;
  width: 100%;
  display:  flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.numberofListsText {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  color: whitesmoke;
  text-shadow: 2px 2px #000;
}
.chooseAmountText{
  font-size: 18px;
  text-align: center;
  font-weight: bold;
  color: whitesmoke;
  text-shadow: 2px 2px #000;
  margin-bottom: 10px;
}

.optionsNumber{
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.innerBottom {
  display: flex;
  align-items: center;
  height: 50%;
}

.singleOptionBtn{
  display: flex;
  flex: 1;
  height: 100%;
  justify-content: center;
  align-items: center;
  border-top: 2pt solid white;
  border-bottom:  2pt solid white;
  border-left: solid white;
  border-right: solid white; 
  pointer-events: auto;
}

.optionNumberText{
  color: whitesmoke;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}

.randomizeBtn{
  height: 20%;
  width: 100%;
  background-color: purple;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
}

.secondScreenMainCont{
  display: flex;
  width: 80%;
  height: 80%;
  align-self: center;
  flex-direction: column;
}

.randomizedListsCont{
  flex: 10;
  background-color: rgba(0,0,0,0.8);
}

.secondScreenBtns{
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.secondScreenBtnsText{
  font-size: 16px;
  color: whitesmoke;
  font-weight: bold;
  margin-left: 10px;
}

.btnImage{
  width: 22px;
  height: 22px;
}

.playlistNameText{
  color: whitesmoke;
  font-weight: bold;
  font-size: 16px;
  margin: 20px 0px;
  text-align: center;
}

.loadingCont{
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(0,0,0,0.8);
}

.loadingText{
  color: whitesmoke;
  font-size: 18px;
}

.sk-spinner {
  transform: scale(0.8);
  margin-top: 50px;
}

.specialTournamentsCont {
  display: flex;
  height: 30%;
  background-color: rgba(0,0,0,0.8);
  align-items: center;
  padding: 0px 10px;
  -webkit-animation: fadein 2s;
  animation: fadein 2s;
}

.specialTournamentsTitle{
  font-size: 14px;
  margin: 0px 10px;
  color: white;
  text-align: center;
}

.mainModalCont{
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.modalTitleText{
  font-size: 18px;
  text-align: center;
  font-weight: bold;
  margin: 20px;
}

.tournamentOptionCont{
  display: flex;
  background-color: #EFEFEF;
  align-items: center;
  width: 100%;
  border-top: 2px solid;
  padding: 15px;
}

.tournamentOptionText{
  font-size: 16px;
  font-weight: bold;
  margin-left: 10px;
}

.backToRegularBtn{
  border: 0px;
  background-color: rgb(200, 200, 200);
}

.shareContentParagraph{
  position: absolute;
  z-index: 1;
  opacity: 0;
  top: 0;
  right: 0;
}

.Toastify__toast {
  width: 85%;
  margin: 0 auto;
}

.customNumberButtonCont {
  display: flex;
  height: 50%;
  justify-content: center;
  align-items: center;
}

.customNumberButton {
  display: flex;
  border: white 2px solid;
  border-radius: 100px;
  background-color: purple;
  justify-content: center;
  align-items: center;
}

.enterCustomNuberText {
  font-size: 16px;
  color: whitesmoke;
  margin: 10px;
}

.customInputCont {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40%;
  flex-direction: column;
}

.customInput {
  width: 50%;
  height: 30%;
  background-color: rgba(0,0,0,0.8);
  color: whitesmoke;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.backCustom {
  padding: 10px 20px;
  border: white 2px solid;
  border-radius: 100px;
  background-color: purple;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 14px;
  font-weight: 600;
}


@media only screen and (min-width: 768px) {
  .numberofListsText {
    font-size: 50px;
  }
  .chooseAmountText {
    font-size: 35px;
  }
  .optionNumberText{
    font-size: 28px;
  }
  .secondScreenBtnsText{
    font-size: 26px;
    margin-left: 15px;
  }
  .btnImage{
    width: 35px;
    height: 35px;
  }
  .playlistNameText{
    font-size: 26px;
    margin: 30px 0px;
  }
  .loadingText{
    font-size: 30px;
  }
  .specialTournamentsCont {
    padding: 0px 25px;
  }
  .specialTournamentsTitle{
    font-size: 26px;
    margin: 0px 25px;
  }
  .modalTitleText{
    font-size: 24px;
    margin: 40px;
  }
  .tournamentOptionCont{
    padding: 25px;
  }
  .tournamentOptionText{
    font-size: 22px;
    margin-left: 20px;
  }
  .Toastify__toast {
    width: 100%;
  }
  .enterCustomNuberText{
    font-size: 28px;
    margin: 20px;
  }
  .customInput{
    width: 30%;
    font-size: 30px;
  }
}

@media only screen and (min-width: 1025px) {
 .mainInner{
    width: 70%;
  }
  .secondScreenMainCont{
    width: 50%;
  }
  .playlistNameText{
    font-size: 27px;
  }
  .mainModalCont{
    width: 60vw;
  }
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 700ms ease-in-out;
}

.ReactModal__Overlay--after-open{
  opacity: 1;
}

.ReactModal__Overlay--before-close{
  opacity: 0;
}
